import { Component } from 'react';
import './App.css';
import { TrinityEngine, THREE } from './engine/4.0/';

// ====================================================================================

class App extends Component {

	componentDidMount() {
		document.querySelector('#root').appendChild(modules.renderer.domElement);
	};

	render() {
		return (
			<div>
				<div className="arimac_load"></div>
				<div className="arimac_logo arimac_logo_white"></div>
			</div>
		);
	};

};

window.addEventListener('resize', function() {
	TrinityEngine.setupScene(modules.renderer, modules.camera)
});

// ====================================================================================

let modules = TrinityEngine.createScene('#020208',false);

modules.scene.add(TrinityEngine.createLight('AMBIENT', '#FFF', 10));


let tex = TrinityEngine.loadTexture('ground.png');

tex.wrapS = tex.wrapT = THREE.Wrapping;

tex.repeat.set(1, 2)

let ground = TrinityEngine.createObject({
	Box : [25, 1, 131],
	MeshBasic : {
		color : '#333',
		transparent : true,
		opacity : 0.7,
		map : tex
	}
}, {
	position : { y : -3.6 }
});

modules.scene.add(ground);




TrinityEngine.loadModel('models/DinDjarinRun.fbx', function(man) {

	man.scale.set(3, 3, 3);
	man.position.set(0, -3, 0);
	TrinityEngine.tweenAnimation(man, 1);
	modules.scene.add(man);

	man.children[0].material = new THREE.MeshBasicMaterial({
		wireframe : true,
		color : '#1f427a',
	});

});

let stars = [];

var star_1 = TrinityEngine.createObject({
	Box : [0.06, 0.06, 5],
	MeshBasic : {
		color :'#ff0066',
		transparent : true
	}
});

var star_2 = TrinityEngine.createObject({
	Box : [0.06, 0.06, 5],
	MeshBasic : {
		color :'#346ecb',
		transparent : true
	}
});

stars = stars.concat(
	TrinityEngine.createParticles("XY", -6, +6, +5, +7, -40, +10, 10, 10, 1, star_1, star_2),
	TrinityEngine.createParticles("XY", -8, -5, -2, +6, -40, +10, 10, 10, 1, star_1, star_2),
	TrinityEngine.createParticles("XY", +5, +8, -2, +6, -40, +10, 10, 10, 1, star_1, star_2)
);

stars.forEach(function(star) { modules.scene.add(star); });



function camUp() {
	TrinityEngine.tweenControls('EXACT', modules.controls,
		{ x : 0, y : 0, z : 0 },
		{ x : 0.357892397768942, y : 2.450440059169965, z : 15.128359347531193 },
		{ rotateSpeed : 0.6 },
		100, camDown
	);
};

function camDown() {
	TrinityEngine.tweenControls('EXACT', modules.controls,
		{ x : 0, y : 0.1, z : 0 },
		{ x : 0.357892397768942, y : 2.450440059169965, z : 15.128359347531193 },
		{ rotateSpeed : 0.6 },
		100, camUp
	);
};

camDown();


const listener = new THREE.AudioListener();
modules.camera.add(listener);

// create a global audio source
const sound = new THREE.Audio(listener);

// load a sound and set it as the Audio object's buffer
const audioLoader = new THREE.AudioLoader();
audioLoader.load('about.mp3', function(buffer) {
	sound.setBuffer(buffer);
	sound.setLoop(true);
	sound.setVolume(0.5);
	window.addEventListener('click', function() {
		if(sound.isPlaying === false) {
			sound.play();
		}
	});
});

function animateStars() {
	stars.forEach(function(s) {
		tex.offset.y -= 0.00003;
		s.position.z -= 0.8;
		s.material.opacity = 1 - (s.position.z / -40);
		if(s.position.z < -40) {
			s.position.z = 10;
			s.material.opacity = 0;
		}
	});
};

let effects = TrinityEngine.createEffect('BLOOM', modules.renderer, modules.scene, modules.camera);

function render() {
	requestAnimationFrame(render);
	//modules.renderer.render(modules.scene, modules.camera);
	effects.composer.render();
	animateStars();
}; render();

// ====================================================================================

export default App;